.sidebar {
	float: left;
	width: 195px; }



.sale {
	display: block;
	margin: 9px 0 16px;
	text-align: center;
	width: 195px;
	height: 48px;
	border-radius: 3px 4px 4px 3px;
	border: 2px solid #21973f;
	color: #4f4e4e;
	font-weight: 500;
	line-height: 48px;
	&:hover {
		text-decoration: none; } }

.bestsellers {
	display: block;
	margin: 10px 0;
	width: 195px;
	height: 48px;
	line-height: 48px;
	text-align: center;
	border-radius: 3px 4px 4px 3px;
	border: 1px solid #e6e6e6;
	background-color: #21973f;
	color: #ffffff;
	font-size: 16px;
	font-weight: 500;
	&:hover {
		text-decoration: none; } }


.filter {
	margin: 10px 0; }
.filter__name {
	position: relative;
	width: 195px;
	height: 48px;
	padding: 14px 0 14px 23px;
	border-radius: 3px 4px 4px 3px;
	border: 1px solid #e6e6e6;
	background-color: #f8f8f8;
	color: #4f4e4e;
	font-size: 16px;
	font-weight: 500;
	line-height: 1.2;
	.icon-plus {
		position: absolute;
		top: 18px;
		right: 14px; }
	&:hover {
		cursor: pointer; } }
.filter__dropdown {
	border: 1px solid #cdcdcd;
	border-top: none;
	padding: 19px 12px;
	width: 99%;
	margin: 0 auto; }
.filter__label {
		display: block;
		color: #5d5d5d;
		font-family: Roboto;
		font-size: 14px;
		font-weight: 400;
		line-height: 1.7;
		margin: 5px 0;
		&:before {
			content: "";
			display: inline-block;
			position: relative;
			top: 3px;
			width: 16px;
			height: 16px;
			border: 2px solid #868686;
			border-radius: 3px;
			margin-right: 10px;
			background-position: center;
			background-repeat: no-repeat; }
		&:hover {
			cursor: pointer; } }
.filter__checkbox {
	display: none; }


.filter__label.is-checked {
	color: #21973f;
	font-family: Roboto;
	font-size: 14px;
	font-weight: 500;
	&:before {
		background-image: url(../img/check.png); } }

.icon-plus {
	font-size: 13px; }
